
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: "Lato";
}
:root{
    --background: #4b5668;
    --text: white;
    --hover: yellow;
}

header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 80px;
    padding: 0 2rem;
    background-color: 	var(--background);
    color: var(--text);
    position:fixed;
    top: 0;
    width: 100%;
}

nav a{
    margin: 0 1rem;
    color: var(--text);
    text-decoration: none;
}
nav a:hover{
    color: var(--hover);
}

header .nav-btn{
    padding: 5px;
    cursor: pointer;
    background: transparent;
    border: none;
    outline: none;
    color: var(--text);
    visibility: hidden;
    opacity: 0;
    font-size: 1.8rem;
}

header .nav-btn-fake{
    padding: 5px;
    cursor: pointer;
    background: transparent;
    border: none;
    outline: none;
    color: var(--text);
    visibility: hidden;
    opacity: 0;
    font-size: 1.8rem;
    visibility: hidden;
}

header div,
nav{
    display: flex;
    align-items: center;
}

@media only screen and (max-width: 1024px){
    header .nav-btn{
        visibility: visible;
        opacity: 1;

    }
    header nav{
        position: fixed;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content:center;
        gap: 1.5rem;
        background-color: var(--background);
        transition: 1s;
        transform: translateY(-100vh);
    }

    header .responsive_nav{
        transform: none;
    }

    nav .nav-close-btn{
        position: absolute;
        top: 2rem;
        right:2rem;
    }
    nav a{
        font-size: 1.5rem;
    }
}